    },
    validateUrea() {
      // eslint-disable-next-line prefer-const
      let output = {
        erm: '',
        err: false,
      };
      if (
        !parseFloat(this.risk.urea) &&
        this.risk.urea !== '' &&
        parseFloat(this.risk.urea) !== 0
      ) {
        output.erm = 'Urea result is not a number';
        output.err = true;
        return output;
      }
      if (this.risk.urea === '') {
        output.err = true;
      }
      if (parseFloat(this.risk.urea) < 0) {
        output.erm = 'Urea result cannot be negative';
        output.err = true;
        return output;
      }
      if (parseFloat(this.risk.urea) >= 90.05) {
        output.erm = 'This model is only validated with urea < 90.1 mmol/L';
        output.err = true;
        return output;
      }
      return output;
    },
    validateAlbumin() {
      // eslint-disable-next-line prefer-const
