      }
      if (parseFloat(this.risk.wcc) < 0) {
        output.erm = 'White cell count cannot be negative';
        output.err = true;
        return output;
      }
      if (parseFloat(this.risk.wcc) < 1 || parseFloat(this.risk.wcc) > 80) {
        output.erm = 'This model is only validated with a WCC between 1 and 80 x10⁹ litre⁻¹';
        output.err = true;
        return output;
      }
      return output;
    },
    validatePulse() {
      // eslint-disable-next-line prefer-const
      let output = {
        erm: '',
        err: false,
      };
      if (
        !parseInt(this.risk.pulse) &&
        this.risk.pulse !== '' &&
        parseInt(this.risk.pulse) !== 0
      ) {
        output.erm = 'Pulse is not a number';
        output.err = true;
        return output;
      }
      if (this.risk.pulse === '') {
        output.err = true;
      }
      if (parseInt(this.risk.pulse) <= 0) {
        output.erm = 'Pulse cannot be zero or negative';
        output.err = true;
        return output;
      }
      if ((this.risk.pulse !== parseInt(this.risk.pulse).toString()) && this.risk.pulse.length > 0) {
        output.erm = 'Pulse must be an integer';
        output.err = true;
        return output;
      }
      if (parseInt(this.risk.pulse) < 40 || parseInt(this.risk.pulse) > 175) {
        output.erm = 'This model is only validated on pulse rates between 40 and 175 bpm';
        output.err = true;
        return output;
      }
      return output;
    },
    validateBP() {
      // eslint-disable-next-line prefer-const
      let output = {
        erm: '',
        err: false,
