      }
      if (parseInt(this.risk.bp) <= 0) {
        output.erm = 'Blood pressure cannot be zero or negative';
        output.err = true;
        return output;
      }
      if (parseInt(this.risk.bp) > 200) {
        output.erm = 'This model is only validated on BP 50-200 mmHg';
        output.err = true;
        return output;
      }
      if (parseInt(this.risk.bp) < 50) {
        output.erm = 'This model is only validated on BP 50-200 mmHg';
        output.err = true;
        return output;
      }
      return output;
    },
