    showResult() {
      console.log(this.result);
      if (this.result) {
        return this.result;
      }
      return {
        percentage: '',
        extra: '',
        ppossum: {
          morbidity: -1,
          mortality: -1,
        },
      };
    },
    go() {
      const nelacalc = new Calculators();
      const calc = nelacalc.NelaV2(nelacalc.harmoniseParams(this.risk));
      console.log({ calc });
      if (!calc) {
        this.open = false;
        return;
      }
      const mortality = calc.mortality;
      let exp = '';
      if (mortality < 5) {
        exp = '';
      } else {
        if (mortality < 10) {
          exp = 'higher';
        } else {
          // greater than or equal to 10%
          exp = 'high';
        }
      }
      this.result = {
        percentage: `${mortality}%`,
        extra: exp,
      };
      this.open = true;
    },
